import * as React from 'react';
import '../../style/Card.css'

const Card = ({ index, name, content, techTags, libTags, imgUrl, readMeUrl }) => {
    return (
            <div id={`card-${index}`} className="card" style={{backgroundImage: 'url('+imgUrl+')'}}>
                <div className="card-content">
                    <h3>{name}</h3>
                    <p >{content}</p>
                    <p className="tags">
                        <label>Tags: </label>
                        {techTags.map(t=>
                            <div className="tag"> {t} </div>)
                        }
                        <br/>
                        {libTags.map(t=>
                            <div className="tag"> {t} </div>)
                        }
                    </p>
                    <a href={readMeUrl} class="read-me" >READ ME</a>
                </div>
            </div>
    );
}
export default Card;